* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

input {
  color: #faebd7;
  width: 200px;
  height: 35px;
  background: linear-gradient(184deg, #e2e165 0%, #44acb1 57%);
  border: none;
  border-radius: 6px;
  margin: 10px auto;
  font-size: 20px;
  display: block;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

input:focus {
  outline: none;
}

::-webkit-input-placeholder {
  color: #faebd7;
  text-align: center;
  font-size: 14px;
}

button {
  width: 120px;
  height: 30px;
  cursor: pointer;
  color: #faebd7;
  background: linear-gradient(9deg, #e2e165 0%, #44acb1 57%);
  border: none;
  border-radius: 8px;
  margin: 0 auto 25px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

button:hover {
  box-shadow: none;
}

button:active {
  color: #000;
}

.hidden {
  position: fixed;
  transform: scale(0);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 25px;
  display: flex;
}

.info {
  justify-content: space-between;
  padding: 4px 12px;
  display: flex;
}

.info-item {
  max-width: 75px;
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 12px;
  display: flex;
}

.info-item:first-child {
  max-width: 55px;
}

img {
  width: 320px;
  height: 240px;
  object-fit: cover;
}

.photo-card {
  border-radius: 4px;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

